import React, { Component } from 'react'
import '../src/App.css'
class SpacePhoto extends Component {
  state = {
    loading: true,
    nasaData: null,
  }
  async componentDidMount() {
    const url = 'https://api.nasa.gov/planetary/apod?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq'
    const response = await fetch(url)
    const data = await response.json()
    this.setState({
      nasaData: data, loading: false
    })
    console.log(data)
  }
  render() {

    return (
      <div className={`App`}>
        <div>{this.state.loading || !this.state.nasaData ? (
          <div className={`Loading`}>
            Loading...
          </div>
        ) : (
          <div>
            <div><h1>{this.state.nasaData.title}</h1></div>
            <div><img src={this.state.nasaData.url} alt='' /></div>
            {/* <div><video width='200px' height='200px' controls >
                    <source src='https://www.youtube.com/embed/VYWjxvm14Pk?rel=0' type='video/WebM' ></source>
                    </video></div> */}
            <div>
              {/* <iframe width="315" height="315"
                src={`https://www.youtube.com/embed/VYWjxvm14Pk?rel=0autoplay=0&showinfo=0&controls=0`} title={`${this.state.nasaData.title}`}>
              </iframe> */}
            </div>
            <div><h5>{this.state.nasaData.explanation}</h5></div>
          </div>
        )}</div>
      </div>
    )
  }
}

export default SpacePhoto
