
import React, { Component } from 'react'
import './App.css'

export class Earth extends Component {
  constructor(props) {
    super(props)
    
    this.state = {
      loading: true,
      nasaData: null,
      i: 0,
      current_image: null,
      populatedArray: [],
      images: ['Pess Next']
    }

    this.decrement = this.decrement.bind(this)
    this.increment = this.increment.bind(this)
    this.reset = this.reset.bind(this)
  }

  async componentDidMount() {
    const url = 'https://api.nasa.gov/EPIC/api/natural?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq'
    const response = await fetch(url)
    const data = await response.json()
    this.setState({
      nasaData: data,
      loading: false      
    })    
    
    this.showImage()
    
    this.populateArray()
  }
  
  showImage(){     
    
    this.setState({      
      images: [         
        <div><img src={`https://api.nasa.gov/EPIC/archive/natural/20${this.state.nasaData[this.state.i].date.charAt(2)}${this.state.nasaData[this.state.i].date.charAt(3)}/${this.state.nasaData[this.state.i].date.charAt(5)}${this.state.nasaData[this.state.i].date.charAt(6)}/${this.state.nasaData[this.state.i].date.charAt(8)}${this.state.nasaData[this.state.i].date.charAt(9)}/png/${this.state.nasaData[this.state.i].image}.png?api_key=s3Ro78SfpZQASJOPCYH3Ggcoby7guKm2mVaqoDgq`} alt='' /><h6>{this.state.nasaData[this.state.i].date}</h6><h3>{this.state.i + 1}</h3></div>          
      ]
    })
  }

  populateArray() {
    let json_data = this.state.nasaData
    let result = []
    let x=0
    for(x in json_data){
      result.push([x, json_data[x]])      
    }
    this.setState({
      populatedArray: result
    })
    console.log(result)
  }

  decrement() {    
    if (this.state.i > 0) {      
      this.setState({
        i: this.state.i - 1        
      },this.showImage)             
    } else {
      this.setState({
        i: this.state.populatedArray.length -1 
      },this.showImage)          
    }   
  }

  increment() {
    if (this.state.i < this.state.populatedArray.length-1 ) {
      this.setState({
        i: this.state.i + 1
      },this.showImage)      
    } else {
      this.setState({
        i: 0
      },this.showImage)      
    }    
  }

  reset() {
    this.setState({
      i: 0
    },this.showImage)    
  }

  render() {
    return (
      <div className={`App`}>
        <div>{this.state.loading || !this.state.nasaData ? (
          <div className={`Loading`}>
            Loading...
          </div>
        ) : (
          <div>
            <h1>Earth</h1>
            <h4>{`${this.state.nasaData[0].date.charAt(5)}${this.state.nasaData[0].date.charAt(6)}`}/{`${this.state.nasaData[0].date.charAt(8)}${this.state.nasaData[0].date.charAt(9)}`}/20{`${this.state.nasaData[0].date.charAt(2)}${this.state.nasaData[0].date.charAt(3)}`}</h4>
            <h6>These images were taken by NASA's EPIC camera onboard the NOAA DSCOVR spacecraft.</h6>
            <div>
              <div>{this.state.images[0]}</div>
              {/* <div>{this.state.i + 1}</div> */}
              <div>
                <button onClick={this.decrement}>
                  Prev
                </button>
                <button onClick={this.reset}>reset</button>
                <button onClick={this.increment}>
                  Next
                </button>
              </div>
            </div>
             <br/>
          </div>
        )}</div>
      </div>
    )
  }
}

export default Earth
